import React from "react";
import { useHistory } from "react-router-dom";


export const SelectorMateria = ({materias, materiaSeleccionada, cuatrimestreSeleccionado}) => {
    const history = useHistory(); 
    const handleChange = (event) => {
        history.push(`/${encodeURIComponent(cuatrimestreSeleccionado)}/${encodeURIComponent(event.target.value)}`)
    }
    if ((!materias) || (materias.length === 0)) {
        return (<div />);
    }
    return (
        <select className="form-control" onChange={handleChange} value={materiaSeleccionada}>
            <option value="" >Elegir materia</option>
            {materias.map(mat =>
                <option key={mat} value={mat}>{mat}</option>)}
        </select>
  );
}
