import React from "react";

export const RespuestasPopupTextoLargo = ({titulo, texto}) => {
  return (
    <div id="cmtModal" className="modal fade" tabIndex="-1" role="dialog"
    aria-labelledby="cmtModalTitle" aria-hidden="true">
    <div className="modal-dialog modal-dialog-scrollable modal-lg" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h4 className="modal-title" id="cmtModalTitle">{titulo}</h4>
          <button type="button" className="close" data-dismiss="modal">&times;</button>
        </div>
        <div className="modal-body">
          <p className="mh-25">{texto}</p>
        </div>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary btn-sm ml-auto"
            data-dismiss="modal">Cerrar</button>
        </div>
      </div>
    </div>
  </div>
  );
}
