import React from "react";

export const RespuestasEncuestaCeldaPuntaje = ({titulo, valor, valorNum}) => {
  const tdClassName = `pje-${ valorNum }`;
  return (
    <td className={tdClassName} title={titulo}>
      <div className="d-xl-none text-center">{valorNum}</div>
      <div className="d-none d-xl-block">{valor}</div>
    </td>
  );
}
