import React from "react";
import {RespuestasEncuestaHeaderCelda} from './RespuestasEncuestaHeaderCelda';


export const RespuestasEncuestaHeader = ({columnas}) => {
    return (
        <thead className="thead-dark">
            <tr>
            {columnas.map(datoCol =>
                <RespuestasEncuestaHeaderCelda key={datoCol.name} 
                titulo={datoCol.title} tituloCorto={datoCol.shortTitle}/>
            )}
            </tr>
        </thead>
  );
}
