import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { SelectorCuatrimestre } from './SelectorCuatrimestre';
import { SelectorMateria } from './SelectorMateria';
import { GetCsvParser } from '../csvparse/GetCsvParser';
import { RespuestasEncuesta } from './RespuestasEncuesta';
import { RespuestasPopupTextoLargo } from './RespuestasPopupTextoLargo';
import { EncuestasDeMateria } from '../services/EncuestasDeMateria';
import { useParams } from "react-router-dom";
  
export const Context = React.createContext();

function Encuestas() {
    // Vienen de la URL
    const { cuatrimestre: cuatrimestreURL, materia: materiaURL} = useParams();
    const cuatrimestreSeleccionado = decodeURIComponent(cuatrimestreURL);
    const materiaSeleccionada = decodeURIComponent(materiaURL);
    // States
    const [cuatrimestres, setCuatrimestres] = useState([]);
    const [materias, setMaterias] = useState([]);
    const [datosEncuestas, setDatosEncuestas] = useState([]);
    const [columnasDatosEncuestas, setColumnasDatosEncuestas] = useState([]);

    const [datosEncuestasDeMateria, setDatosEncuestasDeMateria] = useState({encuestasDeMateria: [], acumulados:{}});
    const [textoLargo, setTextoLargo] = useState("");
    const [tituloTextoLargo, setTituloTextoLargo] = useState("");

    // Cuando hay que mostrar un texto largo
    const handleLongTextChange = (valor, titulo) => {
        setTextoLargo(valor);
        setTituloTextoLargo(titulo);
    }    
    
    // Inicialmente, cargar la lista de los cuatrimestres que hay
    useEffect(() => {
        axios.get("datos/cuatrimestres.json").then((res) => {
            setCuatrimestres(res.data);
        })
      },[]);

    // Ante el cambio en cosas de cuatrimestres
    useEffect(() => {
        // Si falta cargar aun los cuatrimestres, esperar a que lo haga (se volvera a llamar la funcion)
        if (cuatrimestres.length === 0) return;
        if (cuatrimestreSeleccionado === undefined) return;
        const datosCuatSel = cuatrimestres.find(cuat => cuat.id === cuatrimestreSeleccionado);
        if (datosCuatSel === undefined) return;
        axios.get(datosCuatSel.url).then((res) => {
            const parser = GetCsvParser(cuatrimestreSeleccionado, res.data);
            const [materias, datosEncuestas] = parser.parsear();
            const columnas = parser.obtenerColumnas();
            setMaterias(materias);
            setDatosEncuestas(datosEncuestas);
            setColumnasDatosEncuestas(columnas);
        })
    }, [cuatrimestres,cuatrimestreSeleccionado]);

    //Ante el cambio de cosas de materias, cargar sus datos y acumulados
    useEffect( () => {
        if (materiaSeleccionada === "") return;
        if (!datosEncuestas) return;
        const datosEncuestasDeMateria = EncuestasDeMateria(datosEncuestas, materiaSeleccionada, columnasDatosEncuestas);
        console.log("CARGA LOS DATOS", datosEncuestasDeMateria);
        setDatosEncuestasDeMateria(datosEncuestasDeMateria);
    }, [datosEncuestas, materiaSeleccionada, columnasDatosEncuestas]);

    return (
        <Context.Provider
            value={{
                handleLongTextChange
            }}
        >
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h1>Encuestas</h1>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-xl-4 align-self-center">
                        <SelectorCuatrimestre
                            cuatrimestres={cuatrimestres}
                            cuatrimestreSeleccionado={cuatrimestreSeleccionado}
                            materiaSeleccionada={materiaSeleccionada}
                         />
                    </div>
                    <div className="col-12 col-xl-8 align-self-center">
                        <SelectorMateria 
                            materias={materias}
                            cuatrimestreSeleccionado={cuatrimestreSeleccionado}
                            materiaSeleccionada={materiaSeleccionada}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-12" >
                        <RespuestasPopupTextoLargo 
                            titulo={tituloTextoLargo}
                            texto={textoLargo}
                            />
                        <RespuestasEncuesta
                            columnas={columnasDatosEncuestas}
                            datosEncuestas={datosEncuestasDeMateria.encuestasDeMateria}
                            acumuladosPorDocente={datosEncuestasDeMateria.acumulados}
                            />
                    </div>
                </div>
            </div >
        </Context.Provider>
  );
}

export default Encuestas;
