import React from "react";
import {RespuestasEncuestaCelda} from './RespuestasEncuestaCelda';

export const RespuestasEncuestaFilaDatos = ({columnas,datos}) => {
    if (!columnas) return (<div />);
    return (
        <tr>
            {columnas.map(datoCol =>
                    <RespuestasEncuestaCelda key={datoCol.name} 
                        datoCol={datoCol} datos={datos}
                        />
                )}
        </tr>
  );
}
