import React from "react";
import { RespuestasEncuestaCeldaTextoLargo } from './RespuestasEncuestaCeldaTextoLargo';
import { RespuestasEncuestaCeldaPuntaje } from './RespuestasEncuestaCeldaPuntaje';

export const RespuestasEncuestaCelda = ({datoCol, datos}) => {

  const valor = datos[datoCol.name];

  // Para celdas numericas, tambien mandar el valor numerico
  if (datoCol.numeric) {
    const valorNum = datos[datoCol.name + 'Num'];
    return (<RespuestasEncuestaCeldaPuntaje 
      titulo={datoCol.title} valor={valor} valorNum={valorNum} />);
  }

  // Las celdas de texto largo tienen su propio componente
  if (datoCol.longText) {
    return (<RespuestasEncuestaCeldaTextoLargo 
        titulo={datoCol.title} valor={valor} />);
  }

  // Si no, estamos en el componente actual. Armar el class y mostrarlo
  let tdClassName = "table-info";
  if (datoCol.centered) {
    tdClassName = tdClassName + " text-center";
  }
  return (
    <td className={tdClassName} title={datoCol.title}>{valor}</td>
  );
}
