import React from "react";

export const RespuestasEncuestaCeldaAcumulados = ({datoCol, datos}) => {

  if (!datoCol.numeric) {
    return (<td >&nbsp;</td>);
  }
  if (datos[datoCol.name] === undefined)
  {
    return (<td >&nbsp;</td>);
  }
  const valor = datos[datoCol.name].toFixed(2);
  const titulo = `Promedio ${datoCol.title}`;
  return (
    <td className="table-secondary" title={titulo}>{valor}</td>
  );
}
