import React, { useContext } from "react";
import { Context } from "./Encuestas";


const CARACTERES_MOSTRAR_DIRECTO = 10;
export const RespuestasEncuestaCeldaTextoLargo = ({titulo,valor}) => {
  const { handleLongTextChange} = useContext(Context);
  // Si viene sin nada, va en blanco
  if ((valor === undefined) || (valor === "")) {
    return (
      <td className="table-info">&nbsp;</td>
    );
  } 
  // si tiene menos de X caracteres, entonces mostrar directamente el valor
  if (valor && valor.length && valor.length < CARACTERES_MOSTRAR_DIRECTO)  {
    return (
      <td className="table-info text-center">{valor}</td>
    );
  }
  // Si no, usar un boton que maneje un modal
  return (
    <td className="table-info text-center">
      <div className="d-lg-none">
        <button type="button" title={titulo} className="btn btn-sm btn-danger"
              data-toggle="modal" data-target="#cmtModal"
              onClick={() => {handleLongTextChange(valor, titulo)} } >
          <span className="fa fa-eye"></span>
        </button>
      </div>
      <div className="d-none d-lg-block">
        <button type="button" title={titulo} className="btn btn-sm btn-danger"
          data-toggle="modal" data-target="#cmtModal"
          onClick={() => {handleLongTextChange(valor, titulo)} }>
          Ver&nbsp;
          <span className="fa fa-eye"></span>
        </button>
      </div>
    </td>
  );

}
