import * as Papa from './papaparse'
/**
 * Todos los posibles valores de texto y su correspondiente valor numerico
 */
const TEXTOANUM = {
    "excelentes": 5, "excelente": 5, "muy fcil": 5, "muy facil": 5, "muy interesantes": 5,
    "muy buenas":4, "muy bueno": 4, "fcil": 4, "facil": 4,
    "buenas": 3, "bueno": 3, "normal": 3, "interesantes": 3,
    "regulares": 2, "regular": 2, "dificil": 2, "difcil": 2, "poco interesantes": 2,
    "malas": 1, "malo": 1, "muy difcil": 1, "muy dificil": 1, "nada interesantes": 1,
    "no hay tp": 0, "no hay clases prcticas": 0, "no hay clases tericas": 0, 
    "no hay clase tericas":0
}

export class CsvParser {
    constructor(csv) {
      this.csv = csv;
    }

    /**
     * Saca (o intenta sacar, bah) la materia, el curso y el docente de la linea
     * @param  {string} cursoEnCSV - El string con como vienen esos 3 valores en los CSV
     * @return array [materia, curso, docente] con lo mejor que pudo hacer en base a un string sin estructura
     */
    obtenerMateriaCursoYDocente(cursoEnCSV) {
        if (cursoEnCSV === "") return [null,null,null];
        // En general viene separado por guiones
        var materiaCompleta = cursoEnCSV.split("-");
        if (materiaCompleta.length < 2) {
            // Si no, roguemos que venga separado por coma
            materiaCompleta = cursoEnCSV.split(",");
            // Si son exactamente dos cosas, el formato es materia, docente
            if (materiaCompleta.length === 2) {
                return [materiaCompleta[0].trim(),"",materiaCompleta[1].trim()]
            }
            console.log("Imposible parsear curso y docente", cursoEnCSV);
            return ["Error","Error","Error"];
        }
        // Hay un caso particular que es una materia sin docente
        if ((materiaCompleta.length === 2) && (materiaCompleta[0].length === 5)) {
            return [materiaCompleta.join("-").trim(),"","Sin docente"]
        }
        // El docente es lo ultimo de todo
        const docente = materiaCompleta.pop().trim();
        // sacar el curso si lo tiene. Es a veces un numero, a veces "N y N"
        let curso = "";
        if (materiaCompleta.length > 1) {
            let posibleCurso = materiaCompleta[materiaCompleta.length - 1].trim();
            if ((posibleCurso.length > 0) && (!isNaN(posibleCurso.charAt(0)))) {
                curso = materiaCompleta.pop().trim();
            }
        }
        const materia = materiaCompleta.join("-").trim();
        return [materia, curso, docente]
    }
    /**
     * Convierte un texto a valor numerico de 1 a 5 (o 0 si no aplica)
     * @param  {string} texto - El texto a convertir
     * @return 0 si no aplica, 1 a 5 dependiendo del texto, configurado en TEXTOANUM
     */
    textoANum(texto) {
        texto = texto.trim().toLowerCase();
        if (texto in TEXTOANUM) {
            return TEXTOANUM[texto];
        }
        if (texto !== "") console.log("Imposible pasar a texto:", texto);
        return 0;
    }

    
    /**
     * Devuelve un objeto con la clave y el texto, mas otra entrada para el valor numerico del texto
     * @param  {string} clave - La clave que se debe agregar
     * @param  {string} texto - El texto 
     * @return {object} - Un objeto {clave:texto, claveNum: texto pasado a numerico}
     */
    textoYDescripcion(clave,texto) {
        let retObj = {};
        retObj[clave] = texto;
        retObj[clave + 'Num'] = this.textoANum(texto);
        return retObj;
    }

    
    /**
     * Metodo principal: En base a una linea del csv, mapea a array asociativo
     * @param  {} item
     */
    mapLines(lineaCsv) {
        return lineaCsv;
    }
    /**
     * Parsea el CSV y devuelve las materias y las encuestas
     * @return {array} Primero la lista de materias distintas y luego los datos de encuestas
     */
    parsear() {
        const csvLines = Papa.parse(this.csv, {
            header:true , skipEmptyLines: true
            , transformHeader: function(h,i) { return "" + i; } // quiero los headers numericos
        }).data;

        const datosEncuestas = csvLines.map(this.mapLines);

        const materias = [...new Set(datosEncuestas.map(item => item.materia))].sort();
        return [materias, datosEncuestas];
    }


  }