import { CsvParser } from './CsvParser';
export class CsvParserPrePandeima extends CsvParser {

  constructor(csv) {
    super(csv);
    // Gracias javascript por la magia
    this.mapLines = this.mapLines.bind(this)
  }

  obtenerColumnas() {
    return [
      {name:"docente", title:"Docente", shortTitle:"Docente"},
      {name:"general", title:"General", shortTitle:"GEN", numeric:true },
      {name:"temas", title:"Temas", shortTitle:"TEM", numeric:true },
      {name:"teoricas", title:"Tericas", shortTitle:"TEO", numeric:true },
      {name:"practicas", title:"Prcticas", shortTitle:"PRA", numeric:true },
      {name:"dificultadCurso", title:"Dificultad", shortTitle:"DIF", numeric:true },
      {name:"dificultadTP", title:"Dif. TP", shortTitle:"DIFTP", numeric:true },
      {name:"aprobo", title:"Aprobo?", shortTitle:"APR", centered:true},
      {name:"temasActualizados", title:"Actualizada?", shortTitle:"ACT", centered:true},
      {name:"comentarios", title:"Comentarios", shortTitle:"CMT", longText:true},
    ];
  }

  mapLines(item) {
    const [materia, curso, docente] = this.obtenerMateriaCursoYDocente(item[1]);
    return {
      "docente": docente,
      "curso": curso,
      "materia": materia,
      ...this.textoYDescripcion("general",item[2]),
      ...this.textoYDescripcion("temas",item[4]),
      ...this.textoYDescripcion("teoricas",item[6]),
      ...this.textoYDescripcion("practicas",item[7]),
      ...this.textoYDescripcion("dificultadCurso",item[8]),
      ...this.textoYDescripcion("dificultadTP",item[9]),
      "aprobo": item[3],
      "temasActualizados": item[5],
      "comentarios": item[10],

    };
  }  
}