import React from "react";
import {RespuestasEncuestaCeldaAcumulados} from './RespuestasEncuestaCeldaAcumulados';

export const RespuestasEncuestaFilaAcumulados = ({columnas,datos}) => {
    if (!columnas) return (<div />);
    return (
        <tr>
            {columnas.map(datoCol =>
                    <RespuestasEncuestaCeldaAcumulados key={datoCol.name} 
                        datoCol={datoCol} datos={datos}
                        />
                )}
        </tr>
  );
}
