import React from "react";
import {RespuestasEncuestaFilaDatos} from './RespuestasEncuestaFilaDatos';
import {RespuestasEncuestaFilaAcumulados} from './RespuestasEncuestaFilaAcumulados';

export const RespuestasEncuestaFilas = ({columnas,filas,acumuladosPorDocente}) => {
    let filasAMostrar = [];
    const docentes = [...new Set(filas.map(item => item.docente))].sort();
    docentes.forEach((docente) => {
        filasAMostrar = [...filasAMostrar 
            , filas.filter(f => f.docente === docente)
                .map((datos, index) =>
                <RespuestasEncuestaFilaDatos 
                key={docente + index}
                datos={datos}
                columnas={columnas}
                />)
            , (<RespuestasEncuestaFilaAcumulados 
                key={docente + "Acum"}
                datos={acumuladosPorDocente[docente]}
                columnas={columnas}
                />)
            ];
    });

    return (
        <tbody>
            {filasAMostrar}
        </tbody>
  );
}
