const acumularPuntaje = (acumulados, columna, valor) => {
    if (valor === 0) return acumulados;
    if (! (columna in acumulados)) {
        acumulados[columna] = {count:  0, sum: 0}
    }
    acumulados[columna]['count']++;
    acumulados[columna]['sum']+= valor;
    return acumulados;
}

const generarPromedios = (docente, acumulados) => {
    if (docente === null) return {};
    let promedios = {};
    Object.keys(acumulados).forEach((k) => {
        if (acumulados[k]['count'] === 0) return;
        promedios[k] = acumulados[k]['sum'] / acumulados[k]['count'];
    });
    let ret = {};
    ret[docente] = promedios;
    return ret;
}

const generarAcumuladosDeDocente = (datosEncuestas, docente, columnasNumericas) => {
    let acumulados = {};
    datosEncuestas.filter(de => de.docente === docente)
        .forEach((de) => {
        columnasNumericas.forEach(cn => {
            acumulados = acumularPuntaje( acumulados, cn, de[cn + 'Num'] );
        });
    });
    return acumulados
}

const generarAcumulados = (datosEncuestas, datosColumnas) => {
    const columnasNumericas = datosColumnas.filter(dc => dc.numeric).map(dc => dc.name);
    const docentes = [...new Set(datosEncuestas.map(de => de.docente))].sort();
    let promediosDeDocente = {};
    docentes.forEach((docente) => {
        const acumulados = generarAcumuladosDeDocente(datosEncuestas, docente, columnasNumericas);
        promediosDeDocente = {...promediosDeDocente, ...generarPromedios(docente, acumulados)};        
    });
    return promediosDeDocente;
}

export const EncuestasDeMateria = (datosEncuestas, materia, datosColumnas) => {
    if (datosColumnas.length === 0) return {encuestasDeMateria:[], acumulados:{}};
    const encuestasDeMateria = datosEncuestas.filter( 
        de => ( (materia !== null) && (materia !== "") 
            && (de.materia === materia)));
    encuestasDeMateria.sort((a, b) => a.docente.localeCompare(b.docente));
    const acumulados = generarAcumulados(encuestasDeMateria,datosColumnas);
    return {encuestasDeMateria, acumulados};
}