import React from "react";
import { useHistory } from "react-router-dom";

export const SelectorCuatrimestre = ({cuatrimestres, cuatrimestreSeleccionado, materiaSeleccionada}) => {
    const history = useHistory(); 
    const handleChange = (event) => {
        let materiaARedireccionar = (materiaSeleccionada === undefined) ? '' : materiaSeleccionada;
        history.push(`/${encodeURIComponent(event.target.value)}/${encodeURIComponent(materiaARedireccionar)}`)
    }
    return (
        <select className="form-control" value={cuatrimestreSeleccionado} onChange={handleChange}>
            <option value="" >Elegir cuatrimestre</option>
            {cuatrimestres.map(cuat =>
            <option key={cuat.id} value={cuat.id}>{cuat.nombre}</option>)}
        </select>
  );
}
