import { CsvParser } from './CsvParser';
export class CsvParser20202C extends CsvParser {

  obtenerColumnas() {
    return [
      {name:"docente", title:"Docente", shortTitle:"Docente"},
      {name:"general", title:"General", shortTitle:"GEN", numeric:true },
      {name:"temas", title:"Temas", shortTitle:"TEM", numeric:true },
      {name:"teoricas", title:"Tericas", shortTitle:"TEO", numeric:true },
      {name:"practicas", title:"Prcticas", shortTitle:"PRA", numeric:true },
      {name:"dificultadCurso", title:"Dificultad", shortTitle:"DIF", numeric:true },
      {name:"dificultadTP", title:"Dif. TP", shortTitle:"DIFTP", numeric:true },
      {name:"aprobo", title:"Aprobo?", shortTitle:"APR", centered:true},
      {name:"temasActualizados", title:"Actualizada?", shortTitle:"ACT", centered:true},
      {name:"conformeeval", title:"Conforme Eval", shortTitle:"CEV", longText:true},
      {name:"virtualidad", title:"Virtualidad", shortTitle:"VIR", longText:true},
      {name:"comentarios", title:"Comentarios", shortTitle:"CMT", longText:true},
    ];
  }

  acumularDatosConSeparador(actual, titulo, texto) {
    if (texto === "") return actual;
    if (actual !== "") actual = actual + ". ";
    return `${actual}[${titulo}] ${texto}`;
  }

  armarDatosCursadaVirtual({teo, pra, tp, cons, tiempos, altura, pos, neg, hacervirtual}) {
    let ret = "";
    ret = this.acumularDatosConSeparador(ret, "Terica", teo);
    ret = this.acumularDatosConSeparador(ret, "Prctica", pra);
    ret = this.acumularDatosConSeparador(ret, "TPs", tp);
    ret = this.acumularDatosConSeparador(ret, "Consultas", cons);
    ret = this.acumularDatosConSeparador(ret, "Tiempos", tiempos);
    ret = this.acumularDatosConSeparador(ret, "A la altura?", altura);
    ret = this.acumularDatosConSeparador(ret, "Positivos", pos);
    ret = this.acumularDatosConSeparador(ret, "Negativos", neg);
    ret = this.acumularDatosConSeparador(ret, "Al retomar presencialidad hacer virtual?", hacervirtual);
    return ret;
  }


  mapLines = (item) => {
    const [materia, curso, docente] = this.obtenerMateriaCursoYDocente(item[1]);
    return {
      "docente": docente,
      "curso": curso,
      "materia": materia,
      ...this.textoYDescripcion("general",item[2]),
      ...this.textoYDescripcion("temas",item[4]),
      ...this.textoYDescripcion("teoricas",item[6]),
      ...this.textoYDescripcion("practicas",item[7]),
      ...this.textoYDescripcion("dificultadCurso",item[8]),
      ...this.textoYDescripcion("dificultadTP",item[9]),
      "aprobo": item[3],
      "temasActualizados": item[5],
      "comentarios": item[21],
      "conformeeval": item[20],
      "virtualidad": this.armarDatosCursadaVirtual({
          teo: item[11], pra: item[12], tp:item[13], cons:item[14], tiempos:item[15]
          , altura: item[18], pos: item[16], neg: item[17], hacervirtual:item[19] 
        })
    };
  }  
}