import {CsvParserPrePandeima} from './CsvParserPrePandemia';
import {CsvParser20201C} from './CsvParser20201C';
import {CsvParser20202C} from './CsvParser20202C';

export function GetCsvParser(cuat, csv) {
    if (cuat === "20201C") return new CsvParser20201C(csv);
    if (cuat === "20202C") return new CsvParser20202C(csv);
    return new CsvParserPrePandeima(csv);
}