import React from "react";
import { RespuestasEncuestaHeader } from './RespuestasEncuestaHeader';
import { RespuestasEncuestaFilas } from './RespuestasEncuestaFilas';

export const RespuestasEncuesta = ({columnas, datosEncuestas,acumuladosPorDocente}) => {
    if ((!datosEncuestas) || (datosEncuestas.length === 0)) {
        return (<div />);
    }
    return (
        <div className="table-responsive">
            <table className="ng-hide table table-bordered table-sm">
                <RespuestasEncuestaHeader 
                    columnas={columnas}/>
                <RespuestasEncuestaFilas 
                    filas={datosEncuestas} 
                    acumuladosPorDocente={acumuladosPorDocente}
                    columnas={columnas}/>
            </table>
        </div>
  );
}
